<template>
  <div class="textarea-container">
    <h2>yn-textarea</h2>
    <div class="box1">
      <yn-textarea @change="handleChange" @blur="handleChange" @focus="handleChange" v-model="text1" placeholder=""></yn-textarea>
    </div>
    <pre>
      {{focused}}
      {{wrapped}}
      {{ focused ? "" : "" + ""}} {{ wrapped ? "" : "" }} {{ !focused && wrapped ? "" : "" }}
    </pre>
  </div>
</template>
<script type="text/javascript">
export default {
  name: "YnTextareaPage",
  data() {
    return {
      text1: "",
      focused: false,
      wrapped: false
    };
  },
  methods: {
    handleChange(e) {
      this.focused = e.focused;
      this.wrapped = e.wrapped;
    }
  }
};
</script>

<style type="text/css" scoped="scoped">
  .box1 {
    width: 95%;
    margin: 0 auto;
  }
</style>
